 <template>
  <div class="app-container">
    <el-tabs v-model="activeName"
             @tab-click="handleClick">
      <el-tab-pane label="分销员列表"
                   name="first">
        <distributor-list :status="activeName" :user="user" :shop="shop"></distributor-list>
      </el-tab-pane>
      <el-tab-pane label="分销员分组"
                   name="four">
        <distributor-grouping :status="activeName" :user="user" :shop="shop"></distributor-grouping>
      </el-tab-pane>
      <el-tab-pane label="分销员审核"
                   name="second">
        <distributor-audit :status="activeName" :user="user" :shop="shop"></distributor-audit>
      </el-tab-pane>
      <el-tab-pane label="分销员招募"
                   name="three">
        <distributor-recruiting :status="activeName" :user="user" :shop="shop"></distributor-recruiting>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
 <script>
import { mapState } from 'vuex'
import DistributorList from './distributorList'
import DistributorAudit from './distributorAudit'
import DistributorRecruiting from './distributorRecruiting'
import DistributorGrouping from './distributorGrouping'
export default {
  data() {
    return {
      activeName: 'first'
    };
  },
  created() {
    if (this.$route.query.activeName) {
      this.activeName = this.$route.query.activeName
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
      // this.$router.push({query: {activeName: this.activeName}})
    }
  },
  components: {
    DistributorList,
    DistributorAudit,
    DistributorRecruiting,
    DistributorGrouping
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
 </script>
